<template>
    <div>
        <div style="display: flex;justify-content: space-between;">
            <el-button size="small" icon="el-icon-plus" type="primary" @click="showAddSalaryView">添加工资账套</el-button>
            <el-button size="small" icon="el-icon-refresh" type="success" @click="initSalaries"></el-button>
        </div>
        <div style="margin-top: 10px">
            <el-table :data="salaries" border stripe style="width: 100%">
                <el-table-column type="selection" width="70" align="center"></el-table-column>
                <el-table-column width="120" prop="name" label="账套名称" align="center"></el-table-column>
                <el-table-column width="100" prop="basicSalary" label="基本工资" align="center"></el-table-column>
                <el-table-column width="100" prop="trafficSalary" label="交通补助" align="center"></el-table-column>
                <el-table-column width="100" prop="lunchSalary" label="午餐补助" align="center"></el-table-column>
                <el-table-column width="70" prop="bonus" label="奖金" align="center"></el-table-column>
                <el-table-column width="100" prop="createDate" label="启用时间" align="center"></el-table-column>
                <el-table-column label="养老金" align="center">
                    <el-table-column width="70" prop="pensionPer" label="比率" align="center"></el-table-column>
                    <el-table-column width="70" prop="pensionBase" label="基数" align="center"></el-table-column>
                </el-table-column>
                <el-table-column label="医疗保险" align="center">
                    <el-table-column width="70" prop="medicalPer" label="比率" align="center"></el-table-column>
                    <el-table-column width="70" prop="medicalBase" label="基数" align="center"></el-table-column>
                </el-table-column>
                <el-table-column label="公积金" align="center">
                    <el-table-column width="70" prop="accumulationFundPer" label="比率" align="center"></el-table-column>
                    <el-table-column width="70" prop="accumulationFundBase" label="基数" align="center"></el-table-column>
                </el-table-column>
                <el-table-column label="操作" align="center">
                    <template slot-scope="scope">
                        <el-button size="small" @click="showEditSalaryView(scope.row)">编辑</el-button>
                        <el-button size="small" type="danger" @click="deleteSalary(scope.row)">删除</el-button>
                    </template>
                </el-table-column>
            </el-table>
        </div>
        <el-dialog
                :title="dialogTitle"
                :visible.sync="dialogVisible"
                width="40%">
            <div style="height: 300px;display: flex;justify-content: space-around;align-items: center;">
                <div>
                    <el-steps direction="vertical" :active="activeItemIndex">
                        <el-step :title="itemName" v-for="(itemName,index) in salaryItemName" :key="index"></el-step>
                    </el-steps>
                </div>
                <div>
                    <el-input v-model="salary[title]" :placeholder="'请输入'+salaryItemName[index]+'...'"
                              v-for="(value,title,index) in salary"
                              :key="index"
                              v-show="activeItemIndex==index" size="small" style="width: 200px"></el-input>
                </div>
            </div>
            <span slot="footer" class="dialog-footer">
    <el-button @click="preStep">{{activeItemIndex>=1?'上一步':'取 消'}}</el-button>
    <el-button type="primary" @click="nextStep">{{activeItemIndex>=10?'完 成':'下一步'}}</el-button>
  </span>
        </el-dialog>
    </div>
</template>

<script>
    export default {
        name: "SalSob",
        data() {
            return {
                dialogVisible: false,
                dialogTitle: "添加工资账套",
                salaries: [],
                salaryItemName: [
                    '基本工资',
                    '交通补助',
                    '午餐补助',
                    '奖金',
                    '养老金比例',
                    '养老金基数',
                    '医疗保险比例',
                    '医疗保险基数',
                    '公积金比例',
                    '公积金基数',
                    '账套名称'
                ],
                activeItemIndex: 0,
                salary: {
                    basicSalary: null,
                    trafficSalary: null,
                    lunchSalary: null,
                    bonus: null,
                    pensionPer: null,
                    pensionBase: null,
                    medicalPer: null,
                    medicalBase: null,
                    accumulationFundPer: null,
                    accumulationFundBase: null,
                    name: null
                }
            }
        },
        mounted() {
            this.initSalaries();
        }
        ,
        methods: {
            initSalaries() {
                this.getRequest("/salary/sob/").then(resp => {
                    if (resp) {
                        this.salaries = resp;
                    }
                })
            },
            showAddSalaryView() {
                this.dialogTitle = "添加工资账套";
                this.dialogVisible = true;
            },
            nextStep() {
                if (this.activeItemIndex < 10) {
                    this.activeItemIndex++;
                    return;
                }
                if (this.activeItemIndex == 10) {
                    if (this.salary.id) {
                        this.putRequest("/salary/sob/", this.salary).then(resp => {
                            if (resp) {
                                this.initSalaries();
                                this.dialogVisible = false;
                                this.initSalary();
                            }
                        })
                    } else {
                        this.postRequest("/salary/sob/", this.salary).then(resp => {
                            if (resp) {
                                this.initSalaries();
                                this.dialogVisible = false;
                                this.initSalary();
                            }
                        })
                    }
                }
            },
            preStep() {
                if (this.activeItemIndex >= 1) {
                    this.activeItemIndex--;
                } else if (this.activeItemIndex == 0) {
                    //数据初始化
                    this.initSalary();
                    this.dialogVisible = false;
                }
            },
            deleteSalary(data) {
                this.$confirm('此操作将永久删除【' + data.name + '】工资账套, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.deleteRequest("/salary/sob/" + data.id).then(resp => {
                        if (resp) {
                            this.initSalaries();
                            this.initSalary();
                            this.dialogVisible = false;
                        }
                    })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });
            },
            showEditSalaryView(data) {
                this.dialogTitle = "更新工资账套";
                this.dialogVisible = true;
                this.salary.basicSalary = data.basicSalary;
                this.salary.trafficSalary = data.trafficSalary;
                this.salary.lunchSalary = data.lunchSalary;
                this.salary.bonus = data.bonus;
                this.salary.pensionPerpensionBase = data.pensionPerpensionBase;
                this.salary.pensionBase = data.pensionBase;
                this.salary.medicalPermedicalBase = data.medicalPermedicalBase;
                this.salary.medicalBase = data.medicalBase;
                this.salary.accumulationFundPer = data.accumulationFundPer;
                this.salary.accumulationFundBase = data.accumulationFundBase;
                this.salary.name = data.name;
                this.salary.id = data.id;
            },
            initSalary() {
                this.activeItemIndex = 0;
                this.salary = {
                    basicSalary: null,
                    trafficSalary: null,
                    lunchSalary: null,
                    bonus: null,
                    pensionPer: null,
                    pensionBase: null,
                    medicalPer: null,
                    medicalBase: null,
                    accumulationFundPer: null,
                    accumulationFundBase: null,
                    name: null
                }
            }
        }
    }
</script>

<style scoped>

